<template>
    <div class="safety-box">
        <van-nav-bar
            title="安全中心"
            left-text="返回"
            left-arrow
            fixed
            @click-left="back"
        />
        <Bg>
            <!-- 插槽内容 -->
            <div class="item" @click="isShow">
                <div class="item-title">修改密码</div>
                <van-icon name="arrow" />
            </div>
            <div class="item" @click="exit">
                <div class="item-title">注销账号</div>
                <van-icon name="arrow" />
            </div>
            <div class="btn-box">
                <van-button color="#eb2403" size="large" round @click="toLogin">退出登录</van-button>
            </div>
        </Bg>
        <van-popup v-model="show" position="bottom" closeable>
            <div class="password-box">
                <div class="txt">修改密码</div>
                <van-form>
                    <!-- @submit="onSubmit" -->
                    <van-field
                        v-model="usernameOld.password"
                        :type="usernameOldStatus ? 'password' : 'text' "
                        name="old"
                        label="旧密码"
                        placeholder="旧密码(首字母必须为字母)"
                        :right-icon="usernameOldStatus ? 'closed-eye' : 'eye-o'"
                        autocomplete="off"
                        @click-right-icon="togglePassword('usernameOldStatus')"
                    />
                    <van-field
                        v-model="usernameNew.password"
                        :type="usernameNewStatus ? 'password' : 'text' "
                        name="new"
                        label="新密码"
                        placeholder="新密码(首字母必须为字母)"
                        :right-icon="usernameNewStatus ? 'closed-eye' : 'eye-o'"
                        autocomplete="off"
                        @click-right-icon="togglePassword('usernameNewStatus')"
                    />
                    <div style="margin: 16px;">
                        <van-button round block type="info" native-type="submit" @click="sub">提交</van-button>
                    </div>
                </van-form>
            </div>
        </van-popup>
    </div>
</template>

<script>
import Bg from '../components/Bg.vue'
    export default {
        name: "Safety",
        components:{
            Bg
        },
        data(){
            return{
                show:false,
                username: '',
                password: '',
                // 旧
                usernameOld:{
                    password:""
                },
                // 新
                usernameNew:{
                    password:""
                },
                // 是否显示旧密码
                usernameOldStatus:true,
                // 是否显示新密码
                usernameNewStatus:true

            }
        },
        methods:{
            back(){
                this.$router.go(-1);
            },
            toLogin(){
                //获取token
                let tokenString = this.$cookies.get('TOKEN');
                this.axios({
                    method: "POST",
                    url: "/logout",
                    data: {
                        tokenString
                    }
                })
                    .then((res) => {
                        console.log(res);
                    if (res.data.code === "F001") {
                        //当前商品已经被用户收藏
                        this.$toast(res.data.msg);
                        setTimeout( ()=>{
                            this.$router.push({name: 'Login'});
                        },1000)
                    }
                    
                    })
                    .catch((err) => {
                    
                    });
            },
            // onSubmit(values) {
            //     console.log('submit', values);
            //     console.log('submit',values.old);
            // },
            isShow(){
                this.show = true;
            },
             //切换注册密码状态
            togglePassword(key) {
                this[key] = !this[key];
            },
            exit(){
                this.$dialog.confirm({
                    title: '注销账号',
                    message: '是否确定注销账号,一旦注销无法恢复!',
                    }).then(() => {
                        // 确定
                        // console.log("确定");
                         //获取token
                        let tokenString = this.$cookies.get('TOKEN');
                        this.axios({
                            method: "POST",
                            url: "/destroyAccount",
                            data: {
                                tokenString
                            }
                        })
                            .then((res) => {
                                console.log(res);
                            if (res.data.code === "G001") {
                                //当前商品已经被用户收藏
                                this.$toast(res.data.msg);
                                setTimeout( ()=>{
                                    this.$router.push({name: 'Login'});
                                },1000)
                            }
                            
                            })
                            .catch((err) => {
                                console.log(err);
                            });
                    })
                    .catch((err) =>{
                        // 取消
                        console.log("err ==>" , err);
                    });
            },
            // 提交
            sub(){
                if(this.usernameOld.password.length == 0 || this.usernameNew.password.length == 0){
                    this.$toast('请输入内容');
                    return;
                }
                //验证密码
                //支持字母数字组合, 且必须以字母开头, 6-16位
                let registerPasswrodReg = /^[A-Za-z][A-Za-z\d]{5,15}$/;
                if (!registerPasswrodReg.test(this.usernameOld.password)) {
                    this.$toast("密码支持字母数字组合");
                    return;
                }
                // if (!registerPasswrodReg.test(this.usernameNew.password.length)) {
                //     this.$toast("密码支持字母数字组合");
                //     return;
                // }
                // console.log(123);
                 //获取token
                let old = this.usernameOld.password;
                let last = this.usernameNew.password;
                let tokenString = this.$cookies.get('TOKEN');
                this.axios({
                    method: "POST",
                    url: "/updatePassword",
                    data: {
                        tokenString,
                        password:last,
                        oldPassword:old

                    }
                })
                    .then((res) => {
                        console.log(res);
                    if (res.data.code === "E001") {
                        //当前商品已经被用户收藏
                        this.$toast(res.data.msg);
                        setTimeout( ()=>{
                            this.show=false;
                        },1000)
                    }
                    
                    })
                    .catch((err) => {
                    
                    });
            }
        }
    }
</script>

<style lang="less" scoped>
        .safety-box{
            /deep/ .van-button--info{
                background-color: #eb2403;
                border-color: #eb2403;
            }
            .txt{
                margin-left: 10px;
                margin-top: 15px;
                font-size: 14px;
                color: #868b90;
                margin-bottom: 30px;
            }
            .btn-box{
                margin-top: 120px;
            }
            .item-title{
                font-size: 14px;
            }
            .item{
                display: flex;
                align-items: center;
                border-bottom: 1px solid #f8f9f9;
                padding: 10px 0;
                height: 25px;
                &:last-of-type{
                    border-bottom: none;
                }
            }
            /deep/ .van-icon{
                margin-left: auto;
                color: #c0aca4;
                
            }
             /deep/ .van-nav-bar .van-icon {
            color: #eb2403;
            }
            /deep/ .van-nav-bar__text {
            color: #eb2403;
            }
            /deep/ .bg .bg-content[data-v-00f2d816]{
                padding: 10px;
                padding-top: 20px;
            }
        }
</style>